import React, { useContext } from 'react';
import AppContext from '../Context/AppContext';
import { deployToken } from '../Services/api';
import { MAX_SUPPLY } from '../Services/validate';

import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import Tab from './Tab'

const inputLabel = {
  paddingBottom: '20px',
  paddingTop: '15px',
};

const inputStyle = {
  borderBottom: '1px solid #f79007',
  borderTop: 'none',
  paddingTop: '10px',
  paddingLeft: '20px',
};

const formTextAlign = {
  paddingBottom: '30px',
  textAlign: 'left',
};

function Tab4() {
  const { setKey, tokenData, jwt, setJwt } = useContext(AppContext);

  const finishPage = async () => {
    try{
      const data = await deployToken(tokenData, jwt);
      setJwt(data.token);
      setKey('loading');
    } catch (err) {
      alert(err.message);
    }
  };

  const btnStyle = {
    paddingLeft: '50px',
    paddingRight: '50px',
  };

  return (
    <Tab>
      <h2 className="text-center mt-4 mb-4">Parâmetros do Contrato:</h2>
      <Form.Group as={Row} className="text-center mt-4 mb-4">

      <Form.Label size="lg" column="true" md={5} htmlFor="name" style={inputLabel}>Nome do Token</Form.Label>
        <Col md={6}>
          <Form.Control
            readOnly
            style={inputStyle}
            plaintext type="text"
            value={tokenData.name}
            id="name"
            name="tname"
            label="Nome do Token"
          />
            <Form.Text style={formTextAlign}>
            O nome do Token deve ter de 3-25 caracteres. Caracteres alfanuméricos, espaço e hífen são aceitos.
            </Form.Text>
        </Col>

        <Form.Label size="lg" column="true" md={5} htmlFor="symbol" style={inputLabel}>Sigla do Token</Form.Label>
        <Col md={6}>
          <Form.Control readOnly style={inputStyle} plaintext type="text" id="symbol" value={tokenData.symbol} />
            <Form.Text style={formTextAlign}>3-4 caracteres (exemplos: ETH, BTC, BAT, etc.). Sem espaços, somente caracteres alfanuméricos.</Form.Text>
        </Col>
        
        <Form.Label size="lg" column="true" md={5} htmlFor="supply" style={inputLabel}>Fornecimento Total</Form.Label>
        <Col md={6}>
          <Form.Control readOnly style={inputStyle} plaintext type="text" id="supply" value={tokenData.supply} />
            <Form.Text style={formTextAlign}>Quantidade total de tokens a serem gerados. Mínimo de 1 e máximo de { MAX_SUPPLY }.</Form.Text>
        </Col>

        <Form.Label size="lg" column="true" md={5} htmlFor="owner" style={inputLabel}>Usuário</Form.Label>
        <Col md={6}>
          <Form.Control readOnly style={inputStyle} plaintext type="text" id="owner" value={tokenData.owner} />
            <Form.Text style={formTextAlign}>Carteira do Usuário</Form.Text>
        </Col>

        <Col sm={{ span: 6, offset: 3 }} className="text-center mt-4 mb-4">
          <Button size="lg" variant="success" type="button" name="finish" onClick={ finishPage } style={btnStyle} >Deploy (Emissão)!</Button>
        </Col>
        
      </Form.Group>
    </Tab>
  )
}

export default Tab4;
