import React, { useContext } from 'react';
import AppContext from '../Context/AppContext';
import Tab from './Tab';
import Button from 'react-bootstrap/Button';

function Tab6() {
  const {setKey} = useContext(AppContext);

  const handleClick = () => {
    setKey('token')
  };

  const btnStyle = {
    paddingLeft: '50px',
    paddingRight: '50px',
  };

  return (
    <Tab>
      <span>Obrigado por usar nosso gerador de Token</span>
      <h1>Sua transação foram enviadas com sucesso para a rede Ethereum</h1>
      <p>Assim que a mineração terminar, você pode verificar seus novos ativos no Etherscan</p>
      <p>Se precisar de ajuda para verificar o token, entre em contato conosco em {/* {email} */}</p>

      {/* <p>Share with the world:{}</p> */}
      <Button size="lg" variant="success" type="button" onClick={ handleClick } style={btnStyle}>Criar Novo Token!</Button>
    </Tab>
  )
}

export default Tab6;
