import React, { useContext } from 'react';
import Button from 'react-bootstrap/Button';
import AppContext from '../Context/AppContext';

const style = {
  width: '100%',
  display: 'flex',
  justifyContent: 'space-around',
};

const Footer = ({ to, from, avaiable, name }) => {
  const { setKey } = useContext(AppContext);
  const back = () => {
    setKey(from);
  };

  const submit = async () => {
    setKey(to);
  };

  const btnStyle = {
    paddingLeft: '50px',
    paddingRight: '50px',
  };

  return (
    <div style={style} >
      { 
        from && <Button size="lg" variant="danger" column="true" type="button" onClick={ back } style={btnStyle} >Voltar</Button>
      }
      <Button
        size="lg"
        variant="info"
        column="true"
        type="button"
        disabled={ !avaiable }
        onClick={ submit }
        style={btnStyle}
      >
        Próximo
      </Button>
    </div>
  );
}

export default Footer;
