import React, { useContext } from 'react';
import AppContext from '../Context/AppContext';

import Tab from './Tab';

import 'bootstrap/dist/css/bootstrap.min.css';
import Form from 'react-bootstrap/Form';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import Footer from '../Components/Footer';

function Tab1() {
  const { accounts } = useContext(AppContext);

  return (
    <Tab>
      <h1 className="text-center mt-4 mb-4">Bem Vindo(a) ao Assistente de Criação de Token (BETA)</h1>
      <Row className="justify-content-md-center">
        <Form.Group
          as={Row}
          className="text-center mt-2 mb-2"
        >
          <Col md="6">
            <Form.Check
              defaultChecked
              column="true"
              id="token-type"
              label="Fixed Supply ERC-20"
              name="token"
              required
              style={{paddingBottom: '20px', paddingTop: '15px'}}
              type="radio"
              value="token"
            />
          </Col>
          <p
            style={{paddingBottom: '20px', paddingTop: '15px'}}
          >
            O fornecimento total é criado na implantação do contrato, tornando o fornecimento fixo.
          </p>
        </Form.Group>
      </Row>
      <Footer to="attributes" avaiable={ accounts.length } />
    </Tab>
  )
}

export default Tab1;
