import React, { useState } from 'react';

import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

function UseTerms() {
  const [show, setShow] = useState(true);

  const notAccept = "Você deve aceitar esses termos para começar o processo.";

  const useTerms = (
    <ul>
      <li>Creattoken NÃO é responsável pelo comportamento incorreto do contrato no caso de parâmetros errados inseridos pelos usuários.</li>
      <li>Creattoken NÃO é responsável pelo uso do contrato / conta.</li>
      <li>Creattoken NÃO é responsável por erros no código, mas fará de tudo para eliminá-lo o mais rápido possível, caso sejam detectados.</li>
      <li>Creattoken é SOMENTE responsável pela criação e lançá-lo na rede.</li>
      <li>Creattoken NÃO tem acesso ás funções de gestão do contrato e não será capaz de interrompê-lo,
         continuar e fazer qualquer outra coisa, exceto finalizar após a data de término.</li>
    </ul>
  );

  const handleAccept = () => {
    setShow(false)
    localStorage.setItem('useTerms', 'false');
  }

  const handleCancel = () => {
    setShow(true)
    return alert(notAccept);
  }

  return (
    <div>
      <Modal show={show} onHide={handleAccept} backdrop="static">
        <Modal.Header closeButton>
          <Modal.Title>Termos de Uso</Modal.Title>
        </Modal.Header>
        <Modal.Body>
         {useTerms}
        </Modal.Body>
        <Modal.Footer>
          <Button size="lg" variant="success" type="button" onClick={handleAccept}>Concordar</Button>
          <Button size="lg" variant="danger" type="button" onClick={handleCancel}>Discordar</Button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}

export default UseTerms;