import React, { useContext, useEffect, useState } from 'react';
import AppContext from '../Context/AppContext';
/* import Footer from '../Components/Footer'; */

import { deploy } from '../Services/api';

import Tab from './Tab'

const tabStyle = {
  display: 'flex',
  minHeight: '50vh',
  flexDirection: 'column',
  justifyContent: 'space-around',
  alignItems: 'center',
}

function Tab5() {
  const { jwt, setKey } = useContext(AppContext);

  const [contractAddr, setContractAddr] = useState(null);
  const [hash, setHash] = useState(null);

  const btnStyle = {
    paddingLeft: '50px',
    paddingRight: '50px',
    marginRight: '8px',
  };

  const startDeploy = async () => {
    try {
      const { address, hash } = await deploy(jwt);
      setContractAddr(address);
      setHash(hash);
    } catch {
      console.error('Invalid request');
      setKey('token');
    }
  };

  useEffect(() => {
    if (jwt) startDeploy();
  }, [jwt]);

  return (
    <Tab style={ tabStyle }>
      <div>
        {
          !(contractAddr && hash)
            ? <h2> Seu contrato está sendo emitido...</h2>
            : <h2> Sucesso! Seu contrato foi emitido </h2>
        }
      </div>
      {
        !(contractAddr && hash) ? (
          <div style={{ textAlign: 'center' }}>
            <h3>NÃO ATUALIZE OU SAIA DESSA PÁGINA!</h3>
            <p>Atualizar ou sair dessa página irá invalidar o deploy (emissão) do contrato</p>
            <div className="spinner-border" role="status">
              <span className="sr-only">Loading...</span>
            </div>
          </div>
        ) : (
          <div style={{ textAlign: 'center' }}>
            <h3>Informações do Contrato</h3>
            <p>Use essas informações para interagir com seu contrato</p>
            <p>
              <a
                href={`https://ropsten.etherscan.io/address/${contractAddr}`}
                className="btn btn-info"
                rel="noreferrer"
                target="_blank"
                style={btnStyle}
              >
                Endereço do Contrato
              </a>
              <a
                href={`https://ropsten.etherscan.io/tx/${hash}`}
                rel="noreferrer"
                target="_blank"
                className="btn btn-info"
                style={btnStyle}
              >
                Hash
              </a>
              <button
                type="button"
                className="btn btn-success"
                style={btnStyle}
              >
                Download Contrato
              </button>
            </p>
          </div>
        )
      }
      {/* <Footer to="again" avaiable="false" /> */}
    </Tab>
  )
}

export default Tab5
